.footer {
    position: relative;
    width: 100%;
    box-sizing: border-box;
    min-height: 140px;
    padding: 10px;
    margin-top: 40px;
    background-color: gray;
}
.footer a {
    margin: 10px 5px 20px 5px;
    float: right;
}
#copyright {
    position: absolute;
    top: 20px;
    left: 20px;
}
.footer .fab {
    color: black;
}

@media (width: 280px) {
    #copyright {
        position: static;
    }
    .footer a {
        float: none;
    }

    .footer a, #copyright {
        margin-top: 20px;
    }

}